"use client";

import { Header } from "@/components/layout/Header";
import { TurnkeySwapCard } from "@/components/swap/TurnkeySwapCard";
import { AddTokenButton } from "@/components/wallet/AddTokenButton";
import { useTurnkey } from "@turnkey/react-wallet-kit";

export default function AbsSwapPage() {
  const { wallets } = useTurnkey();
  const walletAddress = wallets?.[0]?.accounts?.[0]?.address;

  return (
    <>
      <Header />
      <main className="min-h-screen flex items-center justify-center p-4 pt-24 bg-gradient-to-b from-gray-900 to-black">
        <div className="max-w-4xl w-full grid grid-cols-1 md:grid-cols-2 gap-6">
          <TurnkeySwapCard />
          {walletAddress && <AddTokenButton walletAddressOverride={walletAddress} />}
        </div>
      </main>
    </>
  );
}
